<template>
  <div>
    <div class="form-wrap">
      <label>
        <span></span>
        <input v-model="form.name" type="text" />
      </label>
      <label>
        <span></span>
        <select v-model="form.sex">
          <option value="1"></option>
          <option value="2"></option>
        </select>
      </label>
      <label>
        <span></span>
        <input v-model="form.age" type="number" />
      </label>
      <label>
        <span></span>
        <input v-model="form.autograph" />
      </label>
      <button @click="createHandle"></button>
      <button @click="editSaveHandle" v-if="form.id"></button>
    </div>
    <div class="search-wrap">
      <input type="search" />
      <button @click="getList"></button>
    </div>
    <div class="list-wrap">
      <table>
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="v in list" :key="v.id">
            <td>{{ v.name }}</td>
            <td>{{ formatSex(v.sex) }}</td>
            <td>{{ v.age }}</td>
            <td>{{ v.autograph }}</td>
            <td>{{ new Date(v.createAt) }}</td>
            <td>{{ new Date(v.updateAt) }}</td>
            <td>
              <button @click="editHandle(v)"></button>
              <button @click="deleteHandle(v)"></button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>
<script>
import GDb from "../../plugins/db";

const db = new GDb({
  projectId: "1392",
  filePath: "db.json",
  privateToken: "VDzv_dgzsorpjpTysKvy",
  baseURL: "http://gitlab.sensorsdata.cn/api/v4/",
  userName: "lvyueyang",
  indexing: ["name"],
});
export default {
  name: "App",
  data() {
    return {
      search: {},
      list: [],
      form: {
        name: "",
        sex: "1",
        age: "",
        autograph: "",
      },
    };
  },
  mounted() {
    this.getUserInfo();
    this.getList();
  },
  methods: {
    getList() {
      db.query()
        .then((res) => {
          this.list = res;
        })
        .catch((e) => {
          alert(e.message);
        });
    },
    createHandle() {
      const { name, sex, age, autograph } = this.form;
      db.create({
        name,
        sex,
        age,
        autograph,
      })
        .then((res) => {
          this.getList();
          alert("");
        })
        .catch((e) => {
          alert(e.message);
        });
    },
    editHandle(item) {
      const { id, name, sex, age, autograph } = item;
      this.form.id = id;
      this.form.name = name;
      this.form.sex = sex;
      this.form.age = age;
      this.form.autograph = autograph;
    },
    editSaveHandle() {
      const { name, sex, age, autograph } = this.form;
      db.update(this.form.id, {
        name,
        sex,
        age,
        autograph,
      })
        .then((res) => {
          this.getList();
          alert("");
        })
        .catch((e) => {
          alert(e.message);
        });
    },
    deleteHandle(v) {
      if (!confirm(` ${v.name} `)) return;
      db.delete(v.id)
        .then((res) => {
          this.getList();
          alert("");
        })
        .catch((e) => {
          alert(e.message);
        });
    },
    formatSex(sex) {
      return {
        1: "",
        2: "",
      }[sex];
    },
    getUserInfo() {
      db.http.get("/user");
    },
  },
};
</script>
<style lang="less">
.form-wrap,
.search-wrap {
  margin-bottom: 10px;
  border: 1px solid #ccc;
  padding: 15px 10px;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
  margin-top: 15px;
}

td,
th {
  padding: 10px 15px;
  border: 1px solid #ccc;
}
</style>